.checkbox-div{
  display:flex;
  flex-direction: column;
  align-content: center;
  text-align: center;
}
.toggle-button {
  position: relative;
  width: 80px;
  height: 40px;
  margin: 0;
  vertical-align: top;
  background: #ffffff;
  border: 1px solid #bbc1e1;
  border-radius: 30px;
  outline: none;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition: all 0.3s cubic-bezier(0.2, 0.85, 0.32, 1.2);
}
.toggle-button::after {
  content: "";
  transition: all 0.3s cubic-bezier(0.2, 0.85, 0.32, 1.2);
  display: inline-block;
  position: absolute;
  left: 3px;
  top: 3.25px;

  width: 33px;
  height: 33px;
  background-color:#63b8a2;
  border-radius: 50%;

  transform: translateX(0);
}
.toggle-button:checked::after {
  transform: translateX(calc(100% + 6px));
  background-color: #fff;  
}
.toggle-button:checked {
  background-color: #63b8a2;
  border:none;
}
.text{
  text-transform: uppercase;
  color:white;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  margin-bottom:5px
}