.drum-machine{
  display:flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  border:1px solid black;
}
.wrapper {
  position: relative;
  display:flex;
  padding:50px;
  border:5px solid rgba(1,1,1,0.3);
  border-radius: 20px;
  background: #376372;
}
.instruments {
  margin-left:60px;
  display:flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}
.display{
  display:flex;
  justify-content: center;
  align-items: center;
  background:rgba(1,1,1,0.4);
  color:white;
  padding:20px;
  text-transform: uppercase;
}
.name{
  position: absolute;
  margin-left:-100px;
  color:white;
  left:50%;
  top:5px;
  font-family: CerebriSans-Regular,-apple-system,system-ui,Roboto,sans-serif;
}
.display{
  border-radius: 5px;
  border: 3px solid rgb(2, 43, 2);
  min-width:170px;
  font-family: CerebriSans-Regular,-apple-system,system-ui,Roboto,sans-serif;
  font-weight: bold;
}
.bigDiv{
  position: absolute;
  width:350px;
  height: 300px;
}